/* Estilos generales de todo el proyecto */
body {
    background-color: #ba93d2;
    margin: 0;
    padding: 0;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center
}

/* Estilos para el contenedor principal*/

#app {
    background-color: rgb(209, 147, 181);
    width: 50%;
    padding: 40px;
    border-radius: 10px;
    border: 5px solid #ddd;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    font-family: "DM Serif Text", serif;
  font-weight: 400;
  font-style: normal;
    font-size: 20px;
    text-align:center;

}
 /*Estilos para el formulario*/

 form{
    display: flex;
    justify-content: space-between;
 }

 /*Estilos al input*/

 input {
    width: 75%;
    padding: 8px;
    margin-right: 10px;
    border: 1px solid #ddd;
    border-radius: 3px; 
    font-family: "Ubuntu", sans-serif;
  font-weight: 300;
  font-style: normal;
}

/*Estilos al boton*/

button {
    width: 25%;
    padding: 1%;
    background-color: #653178;
    color: rgb(255, 255, 255);
    border: 2px solid #363636;
    border-radius: 5px solid #ffffff;
    cursor: pointer;
    font-family: "Ubuntu", sans-serif;
  font-weight: 300;
  font-style: bold;

}

/*Estilos de la lista*/

ul{
    list-style: none;
    padding: 0;
    
}

/*Estilos a los elementos de la lista*/

li{
    background-color: rgb(209, 147, 181);
    padding: 10px;
    border: 5px solid #ddd;
    margin-top: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-family: "Lora", serif;
  font-optical-sizing: auto;
  font-weight: weight;
  font-style: normal;
   
    
}

/*Estilos para cuando una tarea este completada*/

li.completed{
    text-decoration: line-through;
    color: #888
}


















